import { writable, get } from "svelte/store";
import * as H from "./HelperPartnerProgram";
import * as U from "./Utility";
import * as ChartsMonthly from "./HelperMonthlyAmountsCharts";
import * as ChartsCurrentMonth from "./HelperCurrentMonthCharts";
import * as Stories from "./HelperSingleStoryData";
import { getCurrentMonthSynthesis } from "./HelperSynthesis";
const partnerProgramStore = writable();
export const partnerProgram = {
    subscribe: partnerProgramStore.subscribe,
    set: (p) => partnerProgramStore.set(p),
    getCurrentMonthDate: () => U.getCurrentMonthDate(get(partnerProgramStore)),
    getMonthlyAmounts: () => H.getMonthlyAmounts(get(partnerProgramStore)),
    getListStories: () => H.getListStories(get(partnerProgramStore)),
    getStoryById: (id) => Stories.getStoryById(get(partnerProgramStore), id),
    getCurrentMonthSynthesis: () => getCurrentMonthSynthesis(get(partnerProgramStore)),
    getChartsData: {
        monthly: {
            earningPerMonth: () => ChartsMonthly.earningPerMonth(get(partnerProgramStore)),
            earningPerStory: () => ChartsMonthly.earningPerStory(get(partnerProgramStore)),
            treemapWordsAndEarning: () => ChartsMonthly.treemapWordsAndEarning(get(partnerProgramStore)),
            scatterWordsAndEarning: () => ChartsMonthly.scatterWordsAndEarning(get(partnerProgramStore)),
            wordPerDay: () => ChartsMonthly.wordPerDay(get(partnerProgramStore)),
        },
        currentMonth: {
            earningPerMonthPub: () => ChartsCurrentMonth.earningPerMonthPub(get(partnerProgramStore)),
            earningPerMonthStory: () => ChartsCurrentMonth.earningPerMonthStory(get(partnerProgramStore)),
        },
    },
};
